@import url("https://fonts.googleapis.com/css?family=Roboto:400,400i,500,500i,700&display=swap");
@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap");
@import url('https://fonts.googleapis.com/css?family=Catamaran&display=swap');

$theme-header-bg: #23384e;
$theme-container-width: 95%;

html,
body {
  font-family: "Montserrat", "Roboto";
  /* width: 100%; */
  min-width: 750px;
}

* {
  box-sizing: border-box;
  /* border: 1px solid red; */
}

.container {
  /* border: 1px solid red; */
  width: $theme-container-width;
  margin: 0 auto;
  padding: 20px 0;
  min-width: 750px;
}

.page-content {
  display: flex;
  color: black;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  font-size: 0.7rem;
  margin-top: 1rem;

  > div:first-child {
    flex: 1 1 auto;
    min-width: 250px;
  }
  > div:nth-child(2) {
    flex: 2 1 auto;
    min-width: 500px;
  }
}

