.page-header {
  background: $theme-header-bg;
  color: white;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  h1, h2 {
    margin: 0 0 5px 0;
    font-weight:  300;
  }
}

