.side-content,
.main-content {
  padding-right: 0rem;
}

.side-content *,
.main-content * {
  margin: 0;
  line-height: 1.2rem;
}

.side-content-header,
.main-content-header {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.side-section,
.main-section {
  margin-bottom: 0.8rem;
}

.side-section ul,
.main-content ul {
  /* padding-left: 16px; */
  list-style: none;
  padding-left: 0;
}

.side-section span,
.main-section span {
  display: inline-block;
}

.bullet {
  font-size: 1.2rem;
  margin-right: 0.4rem;
}

.side-section li,
.main-section li {
  display: flex;
}

.side-section h5,
.main-section h5 {
  font-size: 0.7rem;
  font-weight: bold;
}

.main-content,
.side-content {
  align-self: flex-start;
}

.employer-info {
  font-style: italic;
}
