@import "theme";

.current {
    color: green
}

#sidebarToggle {
    width: $size-38;
    height: $size-50;
    background: $nav-background;
    padding-top: 0;
    padding-left: 10px;
    padding-right: 0;
    display: none;
    span {
        width: 80%;
        height: 3px;
        display: block;
        background: white;
        transition: all 0.8 cubic-bezier(0.810, -0.330, 0.345, 1.375);
        transition-delay: 0.2s;
        transform: none;
        opacity: 1;
        margin: 5px auto;
    }
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

.navbar-brand {
    font-size: 2rem;
    color: $brand-color !important; 
    margin-left: 10px;
    font-weight: 700;
    font-family: 'Montserrat'
}

#sidebarToggle.active span:first-of-type {
    transform: rotate(45deg) translate(4px, 8px);
}

#sidebarToggle.active span:nth-of-type(2) {
    opacity: 0;
}

#sidebarToggle.active span:last-of-type {
    transform: rotate(-45deg) translate(3px, -7px);
}

// SIDE NAGIVATION BAR STYLES

#sidebar {
    min-width: $nav-side-width;
    max-width: $nav-side-width;
    position: fixed;
    height: 100vh;
    background: $nav-background;
    top: $size-74;
    z-index: 999;
    color: $nav-text;
    .sidebar-header {
        padding: $size-20;
        background:$nav-background-highlight;
        a {
            color: $nav-text-highlight;
            font-size: 1em;
            display: block;
            text-decoration: none;
            &:hover {
                color: #275162;
                background: $nav-text-highlight;
            }
        }
    }

    h4 {
        margin-bottom: 0;
    }

    ul {
        
        li {
            a {
                color: #aaadaf;
                padding: 10px;
                font-size: 1.1em;
                display: block;
                text-decoration: none;
                &:hover {
                    color: #275162;
                    background: $nav-text-highlight;
                }
            }
        }
        li.active > a, a[aria-expanded="true"] {
            color: $nav-text-highlight;
            background: $nav-background-highlight;
        }
    }
}

.overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    z-index: -1;
    opacity: 0;
    transition: all 0.5s ease-in-out;
    &.active {
        display: block;
        opacity: 1;
        z-index: 998;
    }
}

#dismiss {
    width: 35px;
    height: 35px;
    position: absolute;
    top: 5px;
    right: 15px;
    text-align: right;
    display: none;
    i {
        color: white;
    }
}

#content {
    margin-top: $size-74;
    margin-left: $nav-side-width;
    padding: $size-20;
    transition: all 0.3s;
}

.nav-link {
    font-size: 1.5rem;
}

.navbar-brand {
