    // object-fit: cover;
}

@media (max-width: 600px) {
    .nav-link {
        font-size: 1rem;
    }

    .navbar-brand {
        font-size: 2rem;
        margin-right: 10px;
    }

    #sidebar {
        top: 74px;
    }

    #content {
        margin-top: 74px;
    }
}

@media (max-width: 992px) {
    #sidebarToggle {
        display: inline-block;
    }
    #sidebar {
        margin-left: -$nav-side-width;
        &.active {
            margin-left: 0;
        }
    }
    #dismiss {
        display: inline;
    }

    #content {
        margin-left: 0;
    }
}